// Notification Page
'use client';

import React, { useState } from 'react';

export function NotificationPage() {
  const [notifications, setNotifications] = useState([
    { id: 1, title: 'Order Shipped', message: 'Your order has been shipped!', read: false },
    { id: 2, title: 'Payment Received', message: 'Your payment was successfully processed.', read: true },
    { id: 3, title: 'New Message', message: 'You have a new message from support.', read: false },
  ]);

  const markAsRead = (id: number) => {
    setNotifications(
      notifications.map((notification) =>
        notification.id === id ? { ...notification, read: true } : notification
      )
    );
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6 bg-gray-50 rounded-lg shadow-md">
      <h2 className="text-2xl font-bold text-gray-800">Notifications</h2>
      {notifications.length > 0 ? (
        <div className="space-y-4">
          {notifications.map(({ id, title, message, read }) => (
            <div
              key={id}
              className={`p-4 rounded-lg shadow-md flex justify-between items-center ${
                read ? 'bg-gray-100' : 'bg-purple-50'
              }`}
            >
              <div>
                <h3 className="font-semibold text-lg text-gray-800">{title}</h3>
                <p className="text-sm text-gray-600">{message}</p>
              </div>
              {!read && (
                <button
                  onClick={() => markAsRead(id)}
                  className="px-4 py-2 text-sm font-medium text-white bg-purple-600 rounded-lg hover:bg-purple-700"
                >
                  Mark as Read
                </button>
              )}
            </div>
          ))}
        </div>
      ) : (
        <p className="text-gray-600">You have no notifications.</p>
      )}
    </div>
  );
}

